import { v4 as uuidv4 } from 'uuid';
export class BridgeFetcher {
    constructor(bridge) {
        this._bridge = bridge;
    }
    async fetch(url, body) {
        const httpPostMessage = {
            command: 'http-post',
            url,
            body,
            messageId: uuidv4(),
        };
        return await this._bridge.sendMessageFromServerAndExpectResponse(httpPostMessage);
    }
}
